* {
  margin: 0px 0px;
  padding: 0;

}



.navbar {
  font-family: 'Lora', serif;
  text-transform: capitalize;
  font-size: 19px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 10px;
}

.nav-link {
  font-weight: 300px;
}

.active {
  border-bottom: 1px solid rgb(253, 76, 0);
}

.navbar-brand {
  font-weight: 510;
  font-size: 20px;
}

.footercss {
  background-color: black;
  color: white;
  margin: 0px 0px;
  min-height: 100%;
}

.main {
  min-height: 80vh;

}

.atag {
  color: white;
  text-decoration: none;
  font-size: 17px;
}

/* signup and login form css */

.signupform {
  min-height: 62vh;
  background-image: linear-gradient(to right top, #00fffa, #67fdff, #98faff, #bff7ff, #def5ff, #e2f1ff, #eeecff, #ffe5ff, #ffd4eb, #ffc6c3, #ffc28f, #fbca5f);

}

.signupcontainer {
  /* border: 2px solid black; */
  border-radius: 5px;
  width: 750px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  background-color: white;
}

.tokendiv {
  /* width: 100px; */
  word-wrap: break-word;
}

.gradient-custom {
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right top, #ffc3ad, #ffcaa5, #ffd39c, #ffde95, #ffea91, #f3f193, #e4f897, #d4ff9f, #b4ffb2, #95ffc8, #78ffdf, #5ffff5);
}


.glow {
  font-size: 40px;
  color: #fff;
  text-align: center;
  -webkit-animation: glow 1s ease-in-out infinite alternate;
  -moz-animation: glow 1s ease-in-out infinite alternate;
  animation: glow 1s ease-in-out infinite alternate;
}


@keyframes glow {
  from {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #e60073, 0 0 20px #e60073, 0 0 25px #e60073, 0 0 30px #e60073, 0 0 35px #e60073;
  }

  to {
    text-shadow: 0 0 10px #fff, 0 0 15px #ff4da6, 0 0 20px #ff4da6, 0 0 25px #ff4da6, 0 0 30px #ff4da6, 0 0 35px #ff4da6, 0 0 40px #ff4da6;
  }
}


@-webkit-keyframes glow {
  from {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #e60073, 0 0 20px #e60073, 0 0 25px #e60073, 0 0 30px #e60073, 0 0 35px #e60073;
  }

  to {
    text-shadow: 0 0 10px #fff, 0 0 15px #ff4da6, 0 0 20px #ff4da6, 0 0 25px #ff4da6, 0 0 30px #ff4da6, 0 0 35px #ff4da6, 0 0 40px #ff4da6;
  }
}








.gradient-custom-2 {
  /* fallback for old browsers */
  background: #a1c4fd;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(to right, rgba(161, 196, 253, 1), rgba(194, 233, 251, 1));

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(to right, rgba(161, 196, 253, 1), rgba(194, 233, 251, 1))
}

#progressbar-1 {
  color: #455A64;
}

#progressbar-1 li {
  list-style-type: none;
  font-size: 10px;
  width: 25%;
  float: left;
  position: relative;
}

#progressbar-1 #step1:before {
  content: "1";
  color: #fff;
  width: 29px;
  margin-left: 22px;
  padding-left: 11px;
}

#progressbar-1 #step2:before {
  content: "2";
  color: #fff;
  width: 29px;
}

#progressbar-1 #step3:before {
  content: "3";
  color: #fff;
  width: 29px;

  text-align: center;
}

#progressbar-1 #step4:before {
  content: "4";
  color: #fff;
  width: 29px;
  margin-right: 22px;
  text-align: center;
}







#progressbar-1 li:before {
  line-height: 29px;
  display: block;
  font-size: 12px;
  background: #455A64;
  border-radius: 50%;
  margin: auto;
}

#progressbar-1 li:after {
  content: '';
  width: 121%;
  height: 2px;
  background: #455A64;
  position: absolute;
  left: 0%;
  right: 0%;
  top: 15px;
  z-index: -1;
}

#progressbar-1 li:nth-child(2):after {
  left: 50%
}

#progressbar-1 li:nth-child(1):after {
  left: 25%;
  width: 121%
}

#progressbar-1 li:nth-child(3):after {
  left: 25%;

}

#progressbar-1 li:nth-child(4):after {
  left: 25%;
  width: 50%;
}

#progressbar-1 li.active:before,
#progressbar-1 li.active:after {
  background: #1266f1;
}

.card-stepper {
  z-index: 0
}

.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}



.aboutustext {
  font-size: 22px;
  background: linear-gradient(to right, #047dff, #00a8a8, #ff9100, #ff0000);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}





/* hover effect */

.card img:hover {
  transform: scale(1.01);
  transition: transform 0.1s ease;
  border-radius: 8px;
}


.myhover:hover {
  transform: scale(1.02);
  transition: transform 0.3s ease;
}























